import React from 'react'
import './style.scss'
import moment from 'moment'
import { modalConfirmDelete } from '@/utils'
import { serviceDeleteTask, serviceUpdateTask } from '@/services'
import {
  Card,
  Button,
  Rate
} from 'antd'

interface Props {
  data: { [key: string]: any },
  reloadData(): void
}

const TaskItem: React.FC<Props> = ({ data, reloadData }) => {

  // 0=, 1=/, 2=
  function handleAction(buttonType: number) {
    if (buttonType === 0) {
      modalConfirmDelete()
      .then(() => {
        serviceDeleteTask(data.id)
        .then(res => {
          if (res.data.success) {
            reloadData()
          }
        })
      })
    } else {
      serviceUpdateTask(data.id, {
        rollback: buttonType === 2 && true
      })
      .then(res => {
        if (res.data.success) {
          reloadData()
        }
      })
    }
  }

  return (
    <Card
      title=""
      hoverable
      className="task-component"
    >
      <p className="content">{data.content}</p>
      <div className="level">
        <span></span>
        <Rate value={data.count} disabled></Rate>
        <p>: {moment(data.createdAt).format('HH:mm:ss')}</p>
      </div>

      <div className="button-wrapper">
        <Button
          type="primary"
          danger
          size="small"
          onClick={handleAction.bind(null, 0)}
        >
          
        </Button>
        {(data.type === 1) && (
          <Button
            type="primary"
            size="small"
            onClick={handleAction.bind(null, 1)}
          >
            
          </Button>
        )}
        {([2, 3].includes(data.type)) && (
          <Button
            type="primary"
            size="small"
            onClick={handleAction.bind(null, 2)}
          >
            
          </Button>
        )}
        {(data.type === 2) && (
          <Button
            type="primary"
            size="small"
            onClick={handleAction.bind(null, 1)}
          >
            
          </Button>
        )}
      </div>
    </Card>
  )
}

export default React.memo(TaskItem)
